import React, { PureComponent } from 'react';
// import {AppSwitcher} from './infrastructure/widgets';
import { InfoSidebar, PostForm } from './UserAction';
import { TokenCtx } from './UserAction';

import './Title.css';
import { PushMessageViewer } from './PushMessage';
import { PollingManager } from './polling';

const flag_re = /^\/\/setflag ([a-zA-Z0-9_]+)=(.*)$/;

class ControlBar extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      search_text: '',
      new_message_count: 0,
    };
    this.set_mode = props.set_mode;

    this.on_change_bound = this.on_change.bind(this);
    this.on_keypress_bound = this.on_keypress.bind(this);
    this.do_refresh_bound = this.do_refresh.bind(this);
    this.do_attention_bound = this.do_attention.bind(this);
    this.do_hot_posts_bound = this.do_hot_posts.bind(this);
    this.on_message_update_bound = this.on_message_update.bind(this);

    this.polling_manager = new PollingManager({
      token: this.props.token,
    });

    this.pubSubKey = null;
  }

  componentDidMount() {
    if (window.location.hash) {
      let text = decodeURIComponent(window.location.hash).substr(1);
      if (text.lastIndexOf('?') !== -1)
        text = text.substr(0, text.lastIndexOf('?')); // fuck wechat '#param?nsukey=...'
      this.setState(
        {
          search_text: text,
        },
        () => {
          this.on_keypress({ key: 'Enter' });
        },
      );
    }

    this.pubSubKey = PubSub.subscribe(
      'MessageCountUpdate',
      this.on_message_update_bound,
    );
  }

  componentWillUnmount() {
    this.polling_manager.cleanup();
    PubSub.unsubscribe('MessageCountUpdate', this.pubSubKey);
  }

  on_message_update(msg, count) {
    this.setState({
      new_message_count: count,
    });
  }

  on_change(event) {
    this.setState({
      search_text: event.target.value,
    });
  }

  on_keypress(event) {
    if (event.key === 'Enter') {
      let flag_res = flag_re.exec(this.state.search_text);
      if (flag_res) {
        let r = confirm('Please confirm:\n' + this.state.search_text);
        if (r === true) {
          if (flag_res[2]) {
            localStorage[flag_res[1]] = flag_res[2];
            alert(
              'Set Flag ' +
                flag_res[1] +
                '=' +
                flag_res[2] +
                '\nYou may need to refresh this webpage.',
            );
          } else {
            delete localStorage[flag_res[1]];
            alert(
              'Clear Flag ' +
                flag_res[1] +
                '\nYou may need to refresh this webpage.',
            );
          }
        }
        return;
      }

      const mode = /#[0-9]+/.test(this.state.search_text)
        ? 'single'
        : this.props.mode !== 'attention'
        ? 'search'
        : 'attention';
      this.set_mode(mode, this.state.search_text || '');
    }
  }

  do_refresh() {
    window.scrollTo(0, 0);
    this.setState({
      search_text: '',
    });
    this.set_mode('list', null);
  }

  do_attention() {
    window.scrollTo(0, 0);
    this.setState({
      search_text: '',
    });
    this.set_mode('attention', null);
  }

  do_hot_posts() {
    window.scrollTo(0, 0);
    this.setState({
      search_text: '',
    });
    this.set_mode('search', '');
  }

  render() {
    return (
      <TokenCtx.Consumer>
        {({ value: token }) => (
          <div className="control-bar">
            <a
              className="no-underline control-btn"
              onClick={this.do_refresh_bound}
            >
              <span className="icon icon-refresh" />
              <span className="control-btn-label"></span>
            </a>
            {!!token && (
              <a
                className="no-underline control-btn"
                onClick={this.do_attention_bound}
              >
                <span className="icon icon-attention" />
                <span className="control-btn-label"></span>
              </a>
            )}
            <a
              className="no-underline control-btn"
              onClick={this.do_hot_posts_bound}
            >
              <span className="icon icon-fire" />
              <span className="control-btn-label"></span>
            </a>
            <input
              className="control-search"
              value={this.state.search_text}
              placeholder={`${
                this.props.mode === 'attention' ? '' : ''
              }  #`}
              onChange={this.on_change_bound}
              onKeyPress={this.on_keypress_bound}
            />
            <a
              className="no-underline control-btn"
              onClick={() => {
                this.props.show_sidebar(
                  process.env.REACT_APP_TITLE,
                  <InfoSidebar show_sidebar={this.props.show_sidebar} />,
                );
              }}
            >
              <span className={'icon icon-' + (token ? 'about' : 'login')} />
              <span className="control-btn-label">
                {token ? '' : ''}
              </span>
            </a>
            {!!token && (
              <a
                className="no-underline control-btn"
                onClick={() => {
                  this.props.show_sidebar(
                    '',
                    <PostForm
                      token={token}
                      action={'dopost'}
                      pid={'new_post'}
                      on_complete={() => {
                        this.props.show_sidebar(null, null, 'clear');
                        this.do_refresh();
                      }}
                    />,
                  );
                }}
              >
                <span className="icon icon-plus" />
                <span className="control-btn-label"></span>
              </a>
            )}
            {!!token && (
              /* use bell icon here, update icomoon */
              <a
                className="no-underline control-btn"
                onClick={() => {
                  this.props.show_sidebar(
                    '',
                    <PushMessageViewer
                      token={token}
                      show_sidebar={this.props.show_sidebar}
                      polling_manager={this.polling_manager}
                    />,
                  );
                }}
              >
                {this.state.new_message_count > 0 && (
                  <div className="control-btn-dot">
                    {this.state.new_message_count}
                  </div>
                )}
                <span className="icon icon-bell" />
                <span className="control-btn-label"></span>
              </a>
            )}
          </div>
        )}
      </TokenCtx.Consumer>
    );
  }
}

export function Title(props) {
  return (
    <div className="title-bar">
      {/* <AppSwitcher appid="hole" /> */}
      <div className="aux-margin">
        <div className="title">
          <p className="centered-line">
            <span
              onClick={() =>
                props.show_sidebar(
                  process.env.REACT_APP_TITLE,
                  <InfoSidebar show_sidebar={props.show_sidebar} />,
                )
              }
            >
              {process.env.REACT_APP_TITLE}
            </span>
          </p>
        </div>
        <TokenCtx.Consumer>
          {({ value: token }) => (
            <ControlBar
              show_sidebar={props.show_sidebar}
              set_mode={props.set_mode}
              mode={props.mode}
              token={token}
            />
          )}
        </TokenCtx.Consumer>
      </div>
    </div>
  );
}
